.navBar {
  font-family: var(--font-primary);
  display: flex;
  align-items: center;
  justify-content: space-around;
  color: var(--color-primary);
  height: 4em;
  width: 100%;
  padding-top: 1vw;
}

.logoNav {
  width: 4vw;
  border-radius: 50%;
  z-index: 2;
}

.navLinks {
  height: 100%;
  width: 60%;
  display: flex;
  flex-wrap: wrap;
  margin-right: 2rem;
  align-items: center;
  justify-content: space-between;
  list-style: none;
}

.navLinks #hidden {
  display: flex;
}

.navLink {
  text-decoration: none;
  color: var(--color-primary);
  font-size: 1.5vw;
  height: 100%;
  border: var(--color-primary) solid 1px;
  box-sizing: border-box;
  border-radius: 50%;
  padding: 0.5em;
  transition-timing-function: 1s cubic-bezier(0.47, 0, 0.745, 0.715);
}

.navLink:hover {
  background-color: var(--color-primary);
  color: var(--color-secondary);
}

.navBar button {
  display: none;
  outline: none;
}

@media only screen and (max-width: 900px) {
  .logoNav {
    width: 10%;
  }
  .navBar {
    height: 8em;
  }
  .navLink {
    font-size: 2.5vw;
   }
}

@media only screen and (max-width: 640px) {
  .navBar button {
    display: flex;
    font-size: 5vw;
    z-index: 2;
    /* border-radius: 50%; */
    /* background-color: beige; */
    color: beige;
    background: transparent;
    border: none;
  }
  .navLinks {
    display: none;
  }
  #hidden {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    /* height: 150%; */
    width: 100%;
    height: 100vh;

    /* background-color: rgba(255, 252, 237, 0.9); */
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 1;
  }

  .navLink {
    /* color: black; */
    color: beige;
    font-weight: 900;
    font-size: 8vw;
    border: none;
  }
}
